.container{
    position: relative;
    height: 64px;
}

.last{
    height: 64px;
    width: 64px;
    position: absolute;
    left: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.absoluteWrapper{
    position: absolute;
    top: 0;
    left: 0;
}

.overlay{
    position: absolute;
    top: 2px;
    left: 2px;
    z-index: 1;
    height: 60px;
    width: 60px;
    border-radius: 100px;
    background: #1C1C21;
    opacity: 0.6;
}

.default0{
    z-index: 7;
    position: absolute;
    top: 0;
    left: 0
}

